// TODO: Standardize the naming convention for message id
export class MessageManager {

    public sendCenterAction() {
        this.sendCustomMessages({ id: 'mfe-map-center' });
    }

    public sendZoomInAction() {
        this.sendCustomMessages({ id: 'mfe-map-zoom-in' });
    }

    public sendZoomOutAction() {
        this.sendCustomMessages({ id: 'mfe-map-zoom-out' });
    }

    public sendGeometries(geometries: any) {
        this.sendCustomMessages({
            id: 'mfe-map-geometries',
            geometries: geometries
        });
    }

    public sendConfig(config: any) {
        this.sendCustomMessages({
            id: 'mfe-configuration',
            ...config
        });
    }

    private sendCustomMessages(data: any) {
        Luigi.customMessages().send(Luigi.elements().getMicrofrontends()[0].id, {
            id: data.id,
            data: data
        });
    }

}