import '@luigi-project/core';
import { MessageManager } from './MessageManager';
import { ActionType } from './constant/ActionType';

interface SemanticObjectParameter {
    key: string;
    value: string;
}

interface ActionProps {
    actionType: string;
    url: string;
    semanticObject: string;
    action: string;
    parameters: SemanticObjectParameter[];
    geoJsonProperties: any;
}

interface Action {
    name: string;
    description: string;
    config: ActionProps;
}

export class Server {

    private messageManager = new MessageManager();
    private luigiCoreEl!: HTMLElement;
    private initialUrl!: string;

    constructor(selector: string) {
        this.luigiCoreEl = document.createElement('DIV');

        // TODO: clean up once Luigi Core Component is ready
        this.luigiCoreEl.style.width = 'var(--_nsc_container_width)';
        this.luigiCoreEl.style.height = 'var(--_nsc_container_height)';
        this.luigiCoreEl.style.position = 'var(--_nsc_container_position)';

        this.luigiCoreEl.setAttribute('luigi-app-root', '');
        setTimeout(() => document.querySelector(selector)?.appendChild(this.luigiCoreEl), 0);

        this.loadConfig();

        this.initialUrl = location.href;
        setTimeout(() => history.replaceState(null, "", this.initialUrl), 0);
    }

    public navigate(nodePath: string) {
        Luigi.navigation().navigate(nodePath);
    }

    public async fetchConfig() {
        return await this.getConfig();
    }

    public onClientLoaded(callback: Function) {
        addEventListener('clientLoaded', () => callback());
    }

    public onClientInitialized(callback: Function) {
        addEventListener('clientInitialized', () => callback());
    }

    public onSemanticObjectNavigation(callback: Function) {
        addEventListener('semanticObjectNavigation', (event: any) => callback(event.detail.semanticObject, event.detail.action, event.detail.params));
    }

    public fireCenter() {
        this.messageManager.sendCenterAction();
    }

    public fireZoomIn() {
        this.messageManager.sendZoomInAction();
    }

    public fireZoomOut() {
        this.messageManager.sendZoomOutAction();
    }

    public fireLoadGeometries(geometries: any) {
        this.messageManager.sendGeometries(geometries);
    }

    private async getConfig() {
        const url = `/nsc/config`;
        const response = await fetch(url);

        try {
            const config = JSON.parse((await response.json())?.value);

            return {
                layers: config?.layers,
                actions: this.constructAction(config?.actions),
                scenarios: config?.scenarios,
                boTypes: config?.boTypes
            };
        } catch (error) {
            console.log(error);
        }
    }

    // TODO: extract the hardcodings
    private loadConfig() {
        Luigi.setConfig({
            navigation: {
                nodes: () => [
                    {
                        pathSegment: 'mapbox',
                        label: 'Map (Mapbox)',
                        hideFromNav: true,
                        hideSideNav: true,
                        viewUrl: 'http://localhost:8082',
                        onNodeActivation: (node: any) => {
                            if (node) {
                                setTimeout(() => history.replaceState(null, "", this.initialUrl), 0);
                                dispatchEvent(new CustomEvent('clientLoaded'));
                            }
                        }
                    }
                ]
            },
            routing: {
                useHashRouting: false
            },
            settings: {
                hideNavigation: true,
                appLoadingIndicator: {
                    hideAutomatically: true
                }
            },
            communication: {
                // TODO: standardize the namespace prefix for listeners
                customMessagesListeners: {
                    'host-action-navigate': (event: any) => {
                        this.actionNavigate({
                            actionType: event.config.actionType_ID,
                            url: event.config.url,
                            semanticObject: event.config.semanticObject_ID,
                            action: event.config.action,
                            parameters: event.config.parameters,
                            geoJsonProperties: event.data.properties
                        });
                    },
                    'mfe-client-initialized': async () => {
                        this.messageManager.sendConfig(await this.fetchConfig());
                        dispatchEvent(new CustomEvent('clientInitialized'));
                    },
                    'mfe-fetch-config': async () => {
                        this.messageManager.sendConfig(await this.fetchConfig());
                    }
                }
            }
        });
    }

    private constructAction(actions: Action[]) {
        const actionItems = actions.map((action: Action) => {
            return {
                text: action.name,
                description: action.description,
                config: action.config,
                fn: (event: any) => {
                    event.postMessage({
                        id: 'host-action-navigate',
                        config: event.config,
                        data: event.data
                    });
                }
            };
        });

        return JSON.stringify(actionItems, (key, value) => {
            if (typeof value === 'function') {
                return '/Function(' + value.toString() + ')/';
            }

            return value;
        });
    }

    private actionNavigate(props: ActionProps) {
        switch (props.actionType) {
            case ActionType.SEMATIC_OBJECT:
                dispatchEvent(new CustomEvent('semanticObjectNavigation', {
                    detail: {
                        semanticObject: props.semanticObject,
                        action: props.action,
                        params: this.contructSemanticObjectParameters(props.parameters, props.geoJsonProperties)
                    }
                }));
                break;
            case ActionType.URL:
                history.pushState(null, '', location.href);
                location.href = this.initialUrl;
                setTimeout(() => location.href = props.url, 0);
                break;
            default:
            // do nothing
        }
    }

    private contructSemanticObjectParameters(parameters: SemanticObjectParameter[], data: any) {
        if (!parameters || !data) return;

        return parameters.reduce((obj: any, item: SemanticObjectParameter) => {
            const key = this.handlebarReplacement(item.key, data);
            const value = this.handlebarReplacement(item.value, data);

            obj[key] = value;
            return obj;
        }, {});
    }

    private handlebarReplacement(value: string, data: any) {
        return value.replace(/\{\{([^}]+)\}\}/g, (match: string) => {
            return data[match.slice(2, -2)];
        });
    }

}